export const playDingSound = () => {
  try {
    const audio = new Audio('/sounds/ding.mp3')
    audio.play().catch(error => {
      console.error('Error playing sound:', error)
    })
  } catch (error) {
    console.error('Error creating audio:', error)
  }
}

