"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select } from "@/components/ui/select"
import PatternEditor from "@/components/PatternEditor"
import BingoGame from "@/components/BingoGame"
import CardValidator from "@/components/CardValidator"
import { GameProvider, useGame } from "@/context/GameContext"

function BingoApp() {
  const { gameSetup, gameState, dispatch } = useGame()
  const [gamePhase, setGamePhase] = useState<"setup" | "pattern" | "game">("setup")

  const handleSetupSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    setGamePhase("pattern")
  }

  const handleStartGame = () => {
    setGamePhase("game")
  }

  return (
    <main className="container mx-auto p-4 min-h-screen bg-gray-50">
      <h1 className="text-4xl font-bold text-center mb-8">75 Ball Bingo</h1>
      
      {gamePhase === "setup" && (
        <Card className="p-6 max-w-2xl mx-auto">
          <h2 className="text-2xl font-semibold mb-4">Game Setup</h2>
          <form onSubmit={handleSetupSubmit} className="space-y-4">
            <div>
              <Label htmlFor="numberOfGames">Number of Games</Label>
              <Input 
                id="numberOfGames"
                type="number"
                min="1"
                value={gameSetup.numberOfGames}
                onChange={(e) => dispatch({
                  type: 'SET_GAME_SETUP',
                  payload: { numberOfGames: parseInt(e.target.value) }
                })}
                className="mt-1"
              />
            </div>

            <div>
              <Label htmlFor="gameType">Game Type</Label>
              <select
                id="gameType"
                value={gameSetup.gameType}
                onChange={(e) => dispatch({
                  type: 'SET_GAME_SETUP',
                  payload: { gameType: e.target.value as any }
                })}
                className="w-full mt-1 p-2 border rounded-md"
              >
                <option value="standard">Standard</option>
                <option value="blackout">Blackout</option>
                <option value="pattern">Pattern</option>
              </select>
            </div>

            <div className="flex items-center space-x-2">
              <input
                type="checkbox"
                id="skipEnabled"
                checked={gameSetup.skipEnabled}
                onChange={(e) => dispatch({
                  type: 'SET_GAME_SETUP',
                  payload: { skipEnabled: e.target.checked }
                })}
                className="rounded"
              />
              <Label htmlFor="skipEnabled">Enable Skip Function</Label>
            </div>

            <Button 
              type="submit"
              className="w-full bg-black hover:bg-gray-800"
            >
              Continue to Pattern Editor
            </Button>
          </form>
        </Card>
      )}

      {gamePhase === "pattern" && (
        <div className="max-w-4xl mx-auto">
          <PatternEditor />
          <Button 
            onClick={handleStartGame}
            className="w-full mt-6 bg-black hover:bg-gray-800"
          >
            Start Game
          </Button>
        </div>
      )}

      {gamePhase === "game" && (
        <div className="max-w-6xl mx-auto">
          <div className="grid gap-6">
            <BingoGame />
            {gameState.gameStatus === 'bingo' && (
              <CardValidator 
                validCards={gameSetup.cards}
                currentPattern={gameSetup.selectedPattern?.grid || []}
                calledNumbers={gameState.calledBalls}
                onValidation={(isValid) => {
                  if (isValid) {
                    dispatch({ type: 'NEXT_GAME' })
                  } else {
                    dispatch({ type: 'SET_GAME_STATE', payload: { gameStatus: 'active' } })
                  }
                }}
              />
            )}
          </div>
        </div>
      )}
    </main>
  )
}

export default function Page() {
  return (
    <GameProvider>
      <BingoApp />
    </GameProvider>
  )
}
