"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"

interface BallInputProps {
  onBallSubmit: (ball: number) => void
  calledNumbers: number[]
  disabled?: boolean
}

export default function BallInput({ 
  onBallSubmit, 
  calledNumbers, 
  disabled = false 
}: BallInputProps) {
  const [inputValue, setInputValue] = useState("")
  const [error, setError] = useState<string | null>(null)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const ball = parseInt(inputValue)

    // Validate input
    if (isNaN(ball) || ball < 1 || ball > 75) {
      setError("Please enter a valid number between 1 and 75")
      return
    }

    if (calledNumbers.includes(ball)) {
      setError("This ball has already been called")
      return
    }

    // Clear input and error, submit ball
    setInputValue("")
    setError(null)
    onBallSubmit(ball)
  }

  return (
    <Card className="p-4">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label 
            htmlFor="ballInput" 
            className="block text-sm font-medium text-gray-700 mb-1"
          >
            Enter Ball Number (1-75)
          </label>
          <div className="flex space-x-2">
            <Input
              id="ballInput"
              type="number"
              min="1"
              max="75"
              value={inputValue}
              onChange={(e) => {
                setInputValue(e.target.value)
                setError(null)
              }}
              className="text-lg"
              placeholder="1-75"
              disabled={disabled}
            />
            <Button 
              type="submit"
              className="bg-black hover:bg-gray-800 px-6"
              disabled={disabled || !inputValue}
            >
              Call
            </Button>
          </div>
        </div>

        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {/* Quick Number Pad */}
        <div className="grid grid-cols-5 gap-2">
          {[1, 2, 3, 4, 5].map((num) => (
            <Button
              key={num}
              type="button"
              variant="outline"
              onClick={() => setInputValue(num.toString())}
              disabled={disabled || calledNumbers.includes(num)}
              className="aspect-square p-0"
            >
              {num}
            </Button>
          ))}
        </div>

        {/* Called Numbers Summary */}
        <div className="text-sm text-gray-600">
          <p>Total Balls Called: {calledNumbers.length}</p>
          <p>Remaining: {75 - calledNumbers.length}</p>
        </div>
      </form>
    </Card>
  )
}
