"use client"

import { Card } from "@/components/ui/card"

interface BingoBoardProps {
  calledNumbers: number[]
  currentBall: number | null
}

export default function BingoBoard({ calledNumbers, currentBall }: BingoBoardProps) {
  // Create a 5x15 grid to represent all possible bingo numbers (1-75)
  const grid: (number | null)[][] = Array(5).fill(null).map((_, rowIndex) =>
    Array(15).fill(null).map((_, colIndex) => {
      const number = rowIndex * 15 + colIndex + 1
      return number <= 75 ? number : null
    })
  )

  const isCalled = (number: number) => calledNumbers.includes(number)
  const isCurrent = (number: number) => currentBall === number

  return (
    <Card className="p-4">
      <h3 className="text-xl font-semibold mb-4">Bingo Board</h3>
      <div className="grid grid-cols-15 gap-1">
        {grid.map((row, rowIndex) => (
          <div key={rowIndex} className="grid grid-cols-15 gap-1 w-full">
            {row.map((number, colIndex) => (
              number && (
                <div
                  key={`${rowIndex}-${colIndex}`}
                  className={`
                    aspect-square flex items-center justify-center text-sm font-medium rounded-full
                    ${isCurrent(number) 
                      ? 'bg-black text-white ring-2 ring-offset-2 ring-black' 
                      : isCalled(number)
                      ? 'bg-gray-800 text-white'
                      : 'bg-white border border-gray-200 hover:bg-gray-50'}
                  `}
                >
                  {number}
                </div>
              )
            ))}
          </div>
        ))}
      </div>

      {/* Called Numbers List */}
      <div className="mt-6">
        <h4 className="font-medium mb-2">Last 5 Called Numbers</h4>
        <div className="flex space-x-2">
          {calledNumbers.slice(-5).reverse().map((number, index) => (
            <div
              key={number}
              className={`
                w-10 h-10 flex items-center justify-center rounded-full text-sm font-bold
                ${index === 0 
                  ? 'bg-black text-white ring-2 ring-offset-2 ring-black' 
                  : 'bg-gray-800 text-white'}
              `}
            >
              {number}
            </div>
          ))}
        </div>
      </div>

      {/* Statistics */}
      <div className="mt-6 grid grid-cols-2 gap-4">
        <div>
          <h4 className="font-medium">Called Numbers</h4>
          <p className="text-2xl font-bold">{calledNumbers.length}</p>
        </div>
        <div>
          <h4 className="font-medium">Remaining</h4>
          <p className="text-2xl font-bold">{75 - calledNumbers.length}</p>
        </div>
      </div>
    </Card>
  )
}
