"use client"

import { createContext, useContext, useReducer, ReactNode } from 'react'
import { 
  GameSetup, 
  GameState, 
  DEFAULT_GAME_SETUP, 
  DEFAULT_GAME_STATE,
  saveGameState,
  loadGameState,
  clearGameState
} from '@/lib/gameUtils'

interface GameContextType {
  gameSetup: GameSetup
  gameState: GameState
  dispatch: React.Dispatch<GameAction>
}

type GameAction =
  | { type: 'SET_GAME_SETUP'; payload: Partial<GameSetup> }
  | { type: 'SET_GAME_STATE'; payload: Partial<GameState> }
  | { type: 'CALL_BALL'; payload: number }
  | { type: 'RESET_TIMER' }
  | { type: 'TICK_TIMER' }
  | { type: 'DECLARE_BINGO' }
  | { type: 'DECLARE_NO_WINNER' }
  | { type: 'NEXT_GAME' }
  | { type: 'RESET_GAME' }

const GameContext = createContext<GameContextType | undefined>(undefined)

function gameReducer(
  state: { gameSetup: GameSetup; gameState: GameState },
  action: GameAction
): { gameSetup: GameSetup; gameState: GameState } {
  switch (action.type) {
    case 'SET_GAME_SETUP':
      const newGameSetup = { ...state.gameSetup, ...action.payload }
      saveGameState(newGameSetup, state.gameState)
      return {
        ...state,
        gameSetup: newGameSetup
      }

    case 'SET_GAME_STATE':
      const newGameState = { ...state.gameState, ...action.payload }
      saveGameState(state.gameSetup, newGameState)
      return {
        ...state,
        gameState: newGameState
      }

    case 'CALL_BALL':
      if (state.gameState.calledBalls.includes(action.payload)) {
        return state
      }
      const updatedGameState = {
        ...state.gameState,
        currentBall: action.payload,
        calledBalls: [...state.gameState.calledBalls, action.payload],
        timer: 10,
        isTimerRunning: true
      }
      saveGameState(state.gameSetup, updatedGameState)
      return {
        ...state,
        gameState: updatedGameState
      }

    case 'RESET_TIMER':
      return {
        ...state,
        gameState: {
          ...state.gameState,
          timer: 10,
          isTimerRunning: true
        }
      }

    case 'TICK_TIMER':
      if (state.gameState.timer <= 0) {
        return {
          ...state,
          gameState: {
            ...state.gameState,
            isTimerRunning: false
          }
        }
      }
      return {
        ...state,
        gameState: {
          ...state.gameState,
          timer: state.gameState.timer - 1
        }
      }

    case 'DECLARE_BINGO':
      const bingoGameState = {
        ...state.gameState,
        gameStatus: 'bingo' as const,
        isTimerRunning: false
      }
      saveGameState(state.gameSetup, bingoGameState)
      return {
        ...state,
        gameState: bingoGameState
      }

    case 'DECLARE_NO_WINNER':
      const noWinnerGameState = {
        ...state.gameState,
        gameStatus: 'noWinner' as const,
        isTimerRunning: false
      }
      saveGameState(state.gameSetup, noWinnerGameState)
      return {
        ...state,
        gameState: noWinnerGameState
      }

    case 'NEXT_GAME':
      if (state.gameSetup.currentGame < state.gameSetup.numberOfGames) {
        const nextGameSetup = {
          ...state.gameSetup,
          currentGame: state.gameSetup.currentGame + 1
        }
        saveGameState(nextGameSetup, DEFAULT_GAME_STATE)
        return {
          gameSetup: nextGameSetup,
          gameState: DEFAULT_GAME_STATE
        }
      }
      return state

    case 'RESET_GAME':
      clearGameState()
      return {
        gameSetup: DEFAULT_GAME_SETUP,
        gameState: DEFAULT_GAME_STATE
      }

    default:
      return state
  }
}

export function GameProvider({ children }: { children: ReactNode }) {
  const savedState = loadGameState()
  const initialState = {
    gameSetup: savedState.gameSetup || DEFAULT_GAME_SETUP,
    gameState: savedState.gameState || DEFAULT_GAME_STATE
  }

  const [state, dispatch] = useReducer(gameReducer, initialState)

  return (
    <GameContext.Provider
      value={{
        gameSetup: state.gameSetup,
        gameState: state.gameState,
        dispatch
      }}
    >
      {children}
    </GameContext.Provider>
  )
}

export function useGame() {
  const context = useContext(GameContext)
  if (context === undefined) {
    throw new Error('useGame must be used within a GameProvider')
  }
  return context
}
