export interface GameSetup {
  numberOfGames: number
  gameType: 'standard' | 'blackout' | 'pattern'
  skipEnabled: boolean
  cards: BingoCard[]
  currentGame: number
  selectedPattern?: Pattern
}

export interface BingoCard {
  serialNumber: string
  batchCode: string
  numbers?: number[][]
}

export interface Pattern {
  id: string
  name: string
  grid: boolean[][]
}

export interface GameState {
  currentBall: number | null
  calledBalls: number[]
  timer: number
  isTimerRunning: boolean
  gameStatus: 'active' | 'bingo' | 'noWinner'
  maxBalls: number
}

export const DEFAULT_GAME_SETUP: GameSetup = {
  numberOfGames: 1,
  gameType: 'standard',
  skipEnabled: false,
  cards: [],
  currentGame: 1
}

export const DEFAULT_GAME_STATE: GameState = {
  currentBall: null,
  calledBalls: [],
  timer: 10,
  isTimerRunning: false,
  gameStatus: 'active',
  maxBalls: 75
}

export const validateBingoCard = (
  card: BingoCard,
  validCards: BingoCard[],
  pattern: Pattern,
  calledNumbers: number[]
): { isValid: boolean; message: string } => {
  // Check if card exists in valid cards
  const validCard = validCards.find(
    c => c.serialNumber === card.serialNumber && c.batchCode === card.batchCode
  )

  if (!validCard) {
    return {
      isValid: false,
      message: "Card not found in registered cards"
    }
  }

  // For pattern validation, we need the card numbers
  if (!validCard.numbers) {
    return {
      isValid: false,
      message: "Card numbers not available for validation"
    }
  }

  // Validate pattern
  for (let i = 0; i < 5; i++) {
    for (let j = 0; j < 5; j++) {
      if (pattern.grid[i][j]) {
        const number = validCard.numbers[i][j]
        if (!calledNumbers.includes(number)) {
          return {
            isValid: false,
            message: "Pattern does not match called numbers"
          }
        }
      }
    }
  }

  return {
    isValid: true,
    message: "Card validated successfully"
  }
}

export const generateRandomCard = (): number[][] => {
  const card: number[][] = Array(5).fill(null).map(() => Array(5).fill(null))
  const usedNumbers = new Set<number>()

  // Generate numbers for each column
  for (let col = 0; col < 5; col++) {
    const min = col * 15 + 1
    const max = min + 14

    for (let row = 0; row < 5; row++) {
      if (row === 2 && col === 2) {
        // Center square is free
        card[row][col] = 0
        continue
      }

      let number: number
      do {
        number = Math.floor(Math.random() * (max - min + 1)) + min
      } while (usedNumbers.has(number))

      usedNumbers.add(number)
      card[row][col] = number
    }
  }

  return card
}

export const saveGameState = (gameSetup: GameSetup, gameState: GameState) => {
  if (typeof window === 'undefined') return

  try {
    window.localStorage.setItem('bingoGameSetup', JSON.stringify(gameSetup))
    window.localStorage.setItem('bingoGameState', JSON.stringify(gameState))
  } catch (error) {
    console.error('Error saving game state:', error)
  }
}

export const loadGameState = (): { 
  gameSetup: GameSetup | null, 
  gameState: GameState | null 
} => {
  if (typeof window === 'undefined') {
    return { gameSetup: null, gameState: null }
  }

  try {
    const setupStr = window.localStorage.getItem('bingoGameSetup')
    const stateStr = window.localStorage.getItem('bingoGameState')
    
    return {
      gameSetup: setupStr ? JSON.parse(setupStr) : null,
      gameState: stateStr ? JSON.parse(stateStr) : null
    }
  } catch (error) {
    console.error('Error loading game state:', error)
    return { gameSetup: null, gameState: null }
  }
}

export const clearGameState = () => {
  if (typeof window === 'undefined') return

  try {
    window.localStorage.removeItem('bingoGameSetup')
    window.localStorage.removeItem('bingoGameState')
  } catch (error) {
    console.error('Error clearing game state:', error)
  }
}

export const isGameOver = (gameState: GameState): boolean => {
  return (
    gameState.gameStatus !== 'active' || 
    gameState.calledBalls.length >= gameState.maxBalls
  )
}

export const formatTime = (seconds: number): string => {
  return `${Math.floor(seconds / 60)}:${(seconds % 60).toString().padStart(2, '0')}`
}
