"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

interface Pattern {
  id: string
  name: string
  grid: boolean[][]
}

export default function PatternEditor() {
  const [patterns, setPatterns] = useState<Pattern[]>([])
  const [currentPattern, setCurrentPattern] = useState<Pattern>({
    id: "",
    name: "",
    grid: Array(5).fill(Array(5).fill(false))
  })

  const toggleCell = (row: number, col: number) => {
    const newGrid = currentPattern.grid.map((r, i) =>
      i === row ? r.map((cell, j) => j === col ? !cell : cell) : [...r]
    )
    setCurrentPattern({ ...currentPattern, grid: newGrid })
  }

  const savePattern = () => {
    if (!currentPattern.name) return
    const newPattern = {
      ...currentPattern,
      id: Date.now().toString()
    }
    setPatterns([...patterns, newPattern])
    setCurrentPattern({
      id: "",
      name: "",
      grid: Array(5).fill(Array(5).fill(false))
    })
  }

  return (
    <div className="space-y-6">
      <div>
        <Label htmlFor="patternName">Pattern Name</Label>
        <Input
          id="patternName"
          value={currentPattern.name}
          onChange={(e) => setCurrentPattern({
            ...currentPattern,
            name: e.target.value
          })}
          className="mt-1"
          placeholder="Enter pattern name"
        />
      </div>

      <div className="grid grid-cols-5 gap-1 max-w-md mx-auto bg-white rounded-lg p-4 shadow-inner">
        {currentPattern.grid.map((row, i) => (
          row.map((cell, j) => (
            <button
              key={`${i}-${j}`}
              className={`w-12 h-12 border-2 border-gray-300 rounded-md transition-colors ${
                cell ? "bg-black" : "hover:bg-gray-100"
              }`}
              onClick={() => toggleCell(i, j)}
            />
          ))
        ))}
      </div>

      <Button 
        onClick={savePattern} 
        className="w-full bg-black hover:bg-gray-800"
        disabled={!currentPattern.name}
      >
        Save Pattern
      </Button>

      <div className="mt-8">
        <h3 className="text-xl font-semibold mb-4">Saved Patterns</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {patterns.map((pattern) => (
            <Card key={pattern.id} className="p-4 hover:shadow-lg transition-shadow">
              <h4 className="font-medium mb-2">{pattern.name}</h4>
              <div className="grid grid-cols-5 gap-1 max-w-[200px] mx-auto">
                {pattern.grid.map((row, i) => (
                  row.map((cell, j) => (
                    <div
                      key={`${i}-${j}`}
                      className={`w-8 h-8 border rounded-sm ${
                        cell ? "bg-black" : "border-gray-300"
                      }`}
                    />
                  ))
                ))}
              </div>
              <div className="mt-4 flex justify-end space-x-2">
                <Button variant="outline" size="sm">Edit</Button>
                <Button variant="destructive" size="sm">Delete</Button>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
