"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"

interface BingoCard {
  serialNumber: string
  batchCode: string
  numbers?: number[][]
}

interface ValidatorProps {
  validCards: BingoCard[]
  currentPattern: boolean[][]
  calledNumbers: number[]
  onValidation: (isValid: boolean) => void
}

export default function CardValidator({ 
  validCards, 
  currentPattern, 
  calledNumbers,
  onValidation 
}: ValidatorProps) {
  const [serialNumber, setSerialNumber] = useState("")
  const [batchCode, setBatchCode] = useState("")
  const [validationResult, setValidationResult] = useState<{
    isValid: boolean
    message: string
  } | null>(null)
  const [showDialog, setShowDialog] = useState(false)

  const validateCard = () => {
    // Check if card exists in valid cards
    const card = validCards.find(
      c => c.serialNumber === serialNumber && c.batchCode === batchCode
    )

    if (!card) {
      setValidationResult({
        isValid: false,
        message: "Invalid card: Card not found in registered cards"
      })
      onValidation(false)
      return
    }

    // If we have the card numbers, validate against pattern
    if (card.numbers) {
      const isPatternValid = validatePattern(card.numbers)
      if (!isPatternValid) {
        setValidationResult({
          isValid: false,
          message: "Invalid card: Pattern does not match"
        })
        onValidation(false)
        return
      }
    }

    setValidationResult({
      isValid: true,
      message: "Card validated successfully! Winner confirmed!"
    })
    onValidation(true)
    setShowDialog(true)
  }

  const validatePattern = (cardNumbers: number[][]) => {
    // Check if the marked numbers in the card match the current pattern
    // and have been called
    for (let i = 0; i < 5; i++) {
      for (let j = 0; j < 5; j++) {
        if (currentPattern[i][j]) {
          const number = cardNumbers[i][j]
          if (!calledNumbers.includes(number)) {
            return false
          }
        }
      }
    }
    return true
  }

  return (
    <div className="space-y-6">
      <Card className="p-6">
        <h3 className="text-xl font-semibold mb-4">Validate Bingo Card</h3>
        <div className="space-y-4">
          <div>
            <Label htmlFor="serialNumber">Serial Number</Label>
            <Input
              id="serialNumber"
              value={serialNumber}
              onChange={(e) => setSerialNumber(e.target.value)}
              className="mt-1"
              placeholder="Enter card serial number"
            />
          </div>
          
          <div>
            <Label htmlFor="batchCode">Batch Code</Label>
            <Input
              id="batchCode"
              value={batchCode}
              onChange={(e) => setBatchCode(e.target.value)}
              className="mt-1"
              placeholder="Enter batch code"
            />
          </div>

          <Button 
            onClick={validateCard}
            className="w-full bg-black hover:bg-gray-800"
            disabled={!serialNumber || !batchCode}
          >
            Validate Card
          </Button>

          {validationResult && (
            <Alert variant={validationResult.isValid ? "default" : "destructive"}>
              <AlertDescription>{validationResult.message}</AlertDescription>
            </Alert>
          )}
        </div>
      </Card>

      <Dialog open={showDialog} onOpenChange={setShowDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Winner Confirmed!</DialogTitle>
          </DialogHeader>
          <div className="p-4">
            <p className="mb-4">The winning card has been validated:</p>
            <ul className="list-disc pl-4 mb-4">
              <li>Serial Number: {serialNumber}</li>
              <li>Batch Code: {batchCode}</li>
            </ul>
            <Button 
              onClick={() => setShowDialog(false)}
              className="w-full bg-black hover:bg-gray-800"
            >
              Close
            </Button>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
