"use client"

import { useEffect } from 'react'
import { useTimer } from '@/hooks/useTimer'
import { Card } from '@/components/ui/card'

interface TimerProps {
  onComplete?: () => void
  isActive?: boolean
  autoStart?: boolean
}

export default function Timer({ 
  onComplete, 
  isActive = true, 
  autoStart = false 
}: TimerProps) {
  const {
    time,
    isRunning,
    start,
    reset,
    formattedTime
  } = useTimer({
    initialTime: 10,
    onComplete,
    autoStart
  })

  useEffect(() => {
    if (isActive && !isRunning) {
      start()
    }
  }, [isActive, isRunning, start])

  // Calculate the progress percentage
  const progress = (time / 10) * 100

  return (
    <Card className="p-4 relative overflow-hidden">
      <div className="relative z-10">
        <h3 className="text-xl font-semibold mb-2">Timer</h3>
        <div className="text-4xl font-bold text-center">{formattedTime}</div>
      </div>
      
      {/* Progress bar background */}
      <div className="absolute bottom-0 left-0 w-full h-1 bg-gray-200"></div>
      
      {/* Animated progress bar */}
      <div 
        className="absolute bottom-0 left-0 h-1 bg-black transition-all duration-1000"
        style={{ width: `${progress}%` }}
      ></div>

      {/* Circular progress indicator */}
      <svg 
        className="absolute top-0 left-0 w-full h-full -rotate-90"
        viewBox="0 0 100 100"
      >
        <circle
          className="text-gray-200"
          strokeWidth="4"
          stroke="currentColor"
          fill="transparent"
          r="45"
          cx="50"
          cy="50"
        />
        <circle
          className="text-black transition-all duration-1000"
          strokeWidth="4"
          strokeDasharray={`${2 * Math.PI * 45}`}
          strokeDashoffset={`${2 * Math.PI * 45 * (1 - progress / 100)}`}
          strokeLinecap="round"
          stroke="currentColor"
          fill="transparent"
          r="45"
          cx="50"
          cy="50"
        />
      </svg>
    </Card>
  )
}
