"use client"

import { useGame } from "@/context/GameContext"
import BingoBoard from "./BingoBoard"
import BallInput from "./BallInput"
import Timer from "./Timer"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { playWinnerSound } from "@/lib/soundUtils"

export default function BingoGame() {
  const { gameState, gameSetup, dispatch } = useGame()

  const handleBallSubmit = (ball: number) => {
    dispatch({ type: 'CALL_BALL', payload: ball })
  }

  const handleBingoCalled = () => {
    dispatch({ type: 'DECLARE_BINGO' })
    playWinnerSound()
  }

  const handleSkip = () => {
    dispatch({ type: 'RESET_TIMER' })
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
      {/* Left Panel - Bingo Board */}
      <div className="md:col-span-2">
        <BingoBoard 
          calledNumbers={gameState.calledBalls}
          currentBall={gameState.currentBall}
        />
      </div>

      {/* Right Panel - Controls */}
      <div className="space-y-6">
        {/* Current Ball Display */}
        {gameState.currentBall && (
          <Card className="p-4 text-center">
            <h3 className="text-xl font-semibold mb-2">Current Ball</h3>
            <div className="w-24 h-24 mx-auto rounded-full bg-black text-white flex items-center justify-center text-4xl font-bold">
              {gameState.currentBall}
            </div>
          </Card>
        )}

        {/* Timer */}
        <Timer 
          isActive={gameState.isTimerRunning}
          onComplete={() => dispatch({ type: 'TICK_TIMER' })}
        />

        {/* Ball Input */}
        <BallInput
          onBallSubmit={handleBallSubmit}
          calledNumbers={gameState.calledBalls}
          disabled={gameState.gameStatus !== 'active'}
        />

        {/* Game Controls */}
        <div className="space-y-2">
          {gameSetup.skipEnabled && (
            <Button
              onClick={handleSkip}
              className="w-full"
              variant="outline"
              disabled={gameState.gameStatus !== 'active'}
            >
              Skip Timer
            </Button>
          )}

          <Button
            onClick={handleBingoCalled}
            className="w-full bg-black hover:bg-gray-800"
            disabled={
              gameState.gameStatus !== 'active' || 
              gameState.calledBalls.length < 5
            }
          >
            Bingo Called!
          </Button>
        </div>

        {/* Game Info */}
        <Card className="p-4">
          <h3 className="text-lg font-semibold mb-2">Game Information</h3>
          <div className="space-y-2 text-sm">
            <p>Game {gameSetup.currentGame} of {gameSetup.numberOfGames}</p>
            <p>Game Type: {gameSetup.gameType}</p>
            <p>Status: {gameState.gameStatus}</p>
            <p>Balls Called: {gameState.calledBalls.length}</p>
            <p>Remaining: {75 - gameState.calledBalls.length}</p>
          </div>
        </Card>
      </div>
    </div>
  )
}
